@import '../../common.scss';

.vault-container {
  padding-top: 120px;
  padding-left: 10%;
  padding-right: 10%;
  background-color: #000;
  color: #fff;
  width: 100vw;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;

  .user-section {
    padding: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;

    .no-tweet-info {
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      font-size: 1rem;
      font-weight: 500;

      .row {
        margin-bottom: 30px;
      }
    }

    .button-container {
      .btn {
        border: 1px solid;
        border-radius: 8px;
        padding: 10px 20px;
        min-width: 200px;
        display: flex;
        justify-content: center;
        transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;

        &:hover {
          cursor: pointer;
          color: #000;
          background-color: #fff;
        }
      }
    }
  }

  .divider {
    width: 100%;
    border-top: 1px solid $text-light-color;
  }
}