@import '../../common.scss';

.leaderboard-container {
  display: flex;
  flex-direction: column;
  align-items: center;

  .title {
    font-size: 2rem;
    padding: 40px;
    letter-spacing: .1rem;
  }

  .description {
    .info {
      color: $text-light-color;
      padding: 0px 20px;
      text-align: center;

      .label {
        color: #fff;
        font-weight: 500;
      }

      .link {
        margin-left: 5px;
        color: rgb(71,161,255);
        text-decoration: underline;

        &:hover {
          color: #1677ff;
        }
      }
    }
    
    .sub-title {
      text-align: center;
      margin: 10px;
    }
  }
}